/**********************************/

/*** thin bar ***/

.thin-bar {
  border-bottom: 5px solid #f33f12;
}

/*** About Section ***/

#about {
  margin-top: 10em;
  border: 1px solid #eee;
  background-color: white;
  padding-bottom: 1em;
}

#about img {
  max-width: 15em;
  position: relative;
  bottom: 7.5em;
}

#about .details {
  margin-top: -2em;
}

#about h2 {
  color: #646464;
  font-size: 3.8em;
  padding-bottom: 1em;
}

#about span {
  font-weight: 300;
  font-size: 0.7em;
}

#about h5 {
  color: #8a8a8b;
}

#about p {
  margin-top: 1em;
  color: #646464;
  font-family: "roboto";
  font-weight: 300;
}

.cta .cta-button {
  color: #cc4b37;
  text-transform: uppercase;
  border: 2px solid rgba(255, 103, 68, 0.91);
  border-radius: 50px;
  background: white;
}

.cta .cta-button:hover,
.cta .cta-button:focus {
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18),
    0 4px 14px 1px rgba(0, 0, 0, 0.15);
  background-color: rgba(255, 103, 68, 0.91);
  color: white;
}

#about i {
  margin-top: 2em;
}

.fa {
  font-size: 1.2em;
  padding: 0.1em 0.4em;
  color: #8a8a8a;
}

.fa:hover {
  cursor: pointer;
}

/*** Experience ***/
#experience {
  background-color: #4c6d86;
  padding: 6em 4em 5.6em;
  height: 350px;
}

#experience h2 {
  color: #fff;
  margin-bottom: 0.7em;
}

#experience .tag-line {
  color: white;
  font-size: 1.4em;
}

#experience i {
  color: rgb(255, 138, 89);
  font-size: 4em;
}

#experience h3 {
  color: #fff;
}

.experience-topics p {
  color: #acaeb2;
  font-family: "Maven pro", sans-serif;
  /*font-weight: 500;*/
}
.experience-topics-front {
  width: 35%;
  float: left;
}
.experience-topics-back {
  width: 40%;
  float: left;
}
.experience-topics-tool {
  width: 20%;
  float: left;
}

.experience-topics ul li {
  list-style: none;
  color: #bbd2ff;
  font-family: "Maven pro", sans-serif;
  font-weight: 500;
}

/*** work ***/
#work {
  padding: 6em 4em 3em;
  background-color: #fff;
}

#work h2 {
  color: #ff6945;
  margin-bottom: 0.7em;
}

#work .tag-line {
  color: #767779;
  font-size: 1.4em;
  margin-bottom: 4em;
}

#work .work-row {
  margin-bottom: 5.5em;
}

.details h3 {
  color: #738182;
  text-transform: uppercase;
}

.details .platform {
  font-size: 0.8em;
  text-transform: uppercase;
  margin: 2em 0 -0.1em;
  font-family: "Maven pro", sans-serif;
  font-weight: 500;
  color: #ff7654;
}

.details i {
  color: #ff7654;
  margin-right: 1em;
}

.details p {
  color: #738182;
  font-family: "Maven pro", sans-serif;
  font-weight: 500;
  font-size: 1.2em;
  margin-bottom: 1.8em;
}

.details a,
.details a:link,
.details a:active {
  color: rgba(255, 103, 68, 0.91);
  border: 2px solid rgba(255, 103, 68, 0.91) !important;
  background-color: #fff;
}

.details a:hover,
.details a:focus {
  /*box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 14px 1px rgba(0, 0, 0, 0.15);*/
  background-color: rgba(255, 103, 68, 0.91) !important;
  color: #fff;
}

/*** teaching ***/
#teaching {
  padding: 6em 4em 3em;
}

#teaching h2 {
  color: #595959;
}

#teaching .tag-line {
  color: #848282;
  font-size: 1.4em;
}

#teaching .teaching-domains {
  height: 500px;
  width: auto;
  margin-top: -4em;
}
.profile-photo {
  width: 200px;
  height: 200px;
  margin: 0 auto;
  border-radius: 50%;
  background-size: cover;
  margin-top: -100px;
  margin-bottom: 40px;
}

/*** Footer Call-to-action ***/
#footer-cta {
  background-color: rgba(255, 103, 68, 0.91);
  padding: 6em 4em 5.6em;
}

#footer-cta h2 {
  color: white;
}

#footer-cta p {
  color: #fff;
  font-family: "Maven pro", sans-serif;
  font-weight: 500;
  font-size: 1.2em;
}

#startButtonDiv {
  margin-top: 3em;
}

#footer-cta a {
  border: 2px solid #fff;
  border-radius: 45px;
  color: #fff;
}

#footer-cta a:hover {
  background: #fff;
  color: rgba(255, 103, 68, 0.91);
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18),
    0 4px 14px 1px rgba(0, 0, 0, 0.15);
}

/*** footer starts ***/

#footer {
  padding: 5em 4em 2em;
}

#footer #logo {
  background-color: rgba(255, 103, 68, 0.91);
  border-radius: 50%;
  color: #fff;
  padding: 0.4em;
  font-family: "Maven pro", sans-serif;
  font-size: 1.6em;
  font-weight: 500;
  display: inline;
}

#footer #copyright {
  margin-top: 1.6em;
  font-family: "Maven pro", sans-serif;
  font-weight: 500;
  color: #7d7d7d;
}

.profile-photo {
  width: 200px;
  height: 200px;
  margin: 0 auto;
  border-radius: 50%;
  background-size: cover;
  margin-top: 30px;
  margin-bottom: 40px;
}

.react-rotating-text-cursor {
  animation: blinking-cursor 0.8s cubic-bezier(0.68, 0.01, 0.01, 0.99) 0s
    infinite;
}

@keyframes blinking-cursor {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
